import React, { Fragment, useEffect } from 'react'
import { useRouter } from 'next/router'
import Head from 'next/head'
import {Container, Col} from 'reactstrap';
import { getBlockStringIndentation } from 'graphql/language/blockString';

const Recibo = () => {

  const router = useRouter();
  const m = router.query.m;
  const p = router.query.p;
  const paypal = `https://paypal.me/2metadigital/${p}`
  const id = router.query.id;
  const factura = `https://app.hubspot.com/quotes/${id}`

  const header = {
    background: 'var(--primary)'
  }
  const button = {
    background: 'var(--primary)',
    color: 'white',
    padding: '1rem 2rem',
    borderRadius: 30,
    display: getBlockStringIndentation,
  }
  const bg = {
    background: 'var(--primary)',
    color: 'white',
    padding: '0 10px'
  }
  const line = {
    lineHeight: 1.5
  }
  const title = {
    lineHeight: 1.5,
    color: 'var(--secondary)',
    fontWeight: 600,
  }
  const step = {
    borderLeft: '3px solid'
  }

  useEffect(() => {
    document.body.style.setProperty('--primary', '#425b76')
    document.body.style.setProperty('--secondary', '#33475b')
    document.body.style.setProperty('--light', '#13B8EA')
    document.body.style.setProperty('--dark', '#4E56F3')
  })

  return (
    <Fragment>
      <Head>
          <title>Presupuesto de Proyecto para {m} - 2MetaDigital</title>

          <meta description='Hacemos páginas web de alto impacto con diseños profesionales redacciones persuasivas enfocadas en ventas que transmiten el valor real de tu producto o servicio.' content='' />
          <meta property="og:description" content="Hacemos páginas web de alto impacto con diseños profesionales redacciones persuasivas enfocadas en ventas que transmiten el valor real de tu producto o servicio." />
          
          <link rel="icon" type="image/png" href="/fav1.png" />
          <meta property="og:image" content="/meta1.png" />
      
          <meta name="robots" content="noindex"/>
      </Head>

      <div style={header}>
        <Container>
          <Col md="8" className='offset-md-2'>
            <img alt="" className="img-fluid p-2" src="/assets/images/logo/logo4neg.png" />
          </Col>
        </Container>
      </div>
      <div className="m-b-20 p-t-100 p-b-100" style={header}>
        <Container>
          <Col md="8" className='offset-md-2'>
            <h1 className="m-0 m-b-20" style={line}><b>Presupuesto de proyecto</b></h1>
            <p className="text-white m-b-10" style={line}>Para: <u>{m}</u></p>
            <p className="text-white" style={line}>Id: {id}</p>
          </Col>
        </Container>
      </div>
      <div className="m-t-40">
        <Container>
          <Col md="8" className='offset-md-2' style={step}>
            <h2 className="m-0 mb-3" style={title}>Presupuesto</h2>
            <p className="m-b-10 mb-4" style={line}>Acceder a los detalles del presupuesto:</p>
            <a style={button} href={factura} target="_blank">Ver Presupuesto →</a>
          </Col>
        </Container>
      </div>
      <div className="mt-5 m-b-20">
        <Container>
          <Col md="8" className='offset-md-2'>
            <h2 className="m-0 mb-4" style={title}>2. Realizar el primer pago (50%):</h2>
            <a style={button} href={paypal} target="_blank">Pagar →</a>
          </Col>
        </Container>
      </div>
      <div className="mt-5 m-b-20">
        <Container>
          <Col md="8" className='offset-md-2'>
            <h2 className="m-0 mb-4" style={title}>3. Realizar el segundo pago (50%):</h2>
            <a style={button} href={paypal} target="_blank">Pagar →</a>
          </Col>
        </Container>
      </div>
      <div className="mt-5 pt-3 pb-2" style={header}>
        <Container>
          <Col md="8" className='offset-md-2'>
            <a className="text-white" href="https://wa.me/541122505698" target="_blank"><u>Contacto</u></a> 
            <span className="text-white d-block">Copyright 2022 - 2MetaDigital</span>
          </Col>
        </Container>
      </div>
    </Fragment>
  )
}

export default Recibo
